.box{
    width: 600px;
    border: 1px solid rgb(161, 34, 12);
    margin: auto;
    margin-top: 20px;
    display: flex;
    border-radius: 10px;
    box-shadow: 1px 1px 1px rgb(161, 54, 12);
}
.leftBox img {
    width: 100px;
    height: 100px;
    display: block;
    margin-bottom: 5px;
}
.leftBox{
    /* border: 1px solid red; */
    text-align: center;  
    padding: 10px 20px;
}
.rightBox{
    width: 100%;
    /* border: 1px solid green; */
    display: flex;
    align-items: center;
    justify-content: space-between;
    padding: 10px 20px;
}
.itemRight{
    align-self: flex-start;
}
span{
    cursor: pointer;
    margin-left: 5px;
}
